import './feed-item.scss'
import React, { Component } from 'react'
import store from '../../redux/store'
import { renderModal } from '../../redux/actions'
import ReactCSSTransitionGroup from 'react-addons-css-transition-group'

export default class FeedItem extends Component {
  constructor(props) {
    super(props)
    this.handleClick = this.handleClick.bind(this)
  }

  handleClick() {
    store.dispatch(renderModal({
      name: this.props.name,
      path: this.props.path,
      href: this.props.href,
      alt: this.props.alt
    }))
  }

  render() {
    return (
      <div className="feed__item" onClick={this.handleClick}>
        <ReactCSSTransitionGroup
        transitionName="item"
        transitionAppear={true}
        transitionAppearTimeout={500}
        transitionEnterTimeout={500}
        transitionLeaveTimeout={300}>
          <div className="feed__item__img" style={{backgroundImage: 'url(' + this.props.path + ')'}}></div>
          <h2 className="feed__item__h2">{this.props.name}</h2>
        </ReactCSSTransitionGroup>
      </div>
    )
  }
}



